.col-max-height {
	max-height: 200px;
}

.image-container {
	height: 200px;
}

.ad-image {
	object-fit: cover;
	border-radius: 4px;
	z-index: -1;
}

.line {
	height: 15px;
	width: 1px;
	background: var(--neutral-2);
}

.border-verified {
	border: 1px solid var(--success-3);
}

.icon-14px {
	font-size: 14px;
	width: 14px;
	height: 14px;
}

.h-25px {
	height: 25px;
}
